@import '../../variables.scss';

*{
    font-family: 'Roboto', sans-serif;
}


.hamburger{
    display: none;  
}
.menu{
    position: absolute;
    margin-top: 90px;
    top: 0;
    width: 100%;
    background: $mainyellow;
    border-bottom: 2px solid $maincolor;
    border-top: 2px solid $maincolor;
    // transition: 0.2s all;
    z-index: 100;
    &__link{
        max-width: 150px;
        font-size: 17px;
        text-transform: uppercase;
        a{
            text-decoration: none;
            color: $maincolor;
            font-weight: bold;
            
            &:hover{
                text-decoration: underline;
            }
        }
    }
    &__links{
        margin: 0 auto;
        display: flex; 
        list-style: none; 
        justify-content: space-around;
        align-items: center;
        padding: 15px;
    }
    &__phone{
        border: 1px solid black;
        padding: 5px 25px;
        border-radius: 10px;
        font-size: 17px;
        font-weight: bold;
        cursor: pointer;
        transition: 0.2s all;
        &:hover{
            background-color: $maincolor;
            a{
                color:$mainyellow;

            }
           
        }
        &:active{
            background-color: #fff;

        }
        a{
            display: flex;
            text-transform: uppercase;
            align-items: center;
            justify-content: space-between;
            text-decoration: none;
            color:$maincolor;

        }
        img{
            max-width: 20px;
            margin-right: 5px;
        }
    }
   
}