@import '../../../variables.scss';

.skills{
    min-height: 1000px;
    &__title{
        width: 100%;
        max-height: 200px;
        height: 100%;
        background-color:$mainyellow ;
        font-size: 20px;
        padding: 30px 10px;
        color: #fff;
        margin-bottom: 80px;
        h2{
            width: 80%;
            text-align: center;
            margin: 0 auto;

        }
        
    }
    &__wrapper{
        text-align: center;
        display: grid;
        grid-template-columns: repeat(2,500px);
        justify-content: center;
        gap: 40px;
        margin-bottom: 50px;
    }
    &__descr{
        max-width: 500px;
        background-color:$mainyellow ;
        
    }
    &__img{
        max-width:500px;
        
        img{
            width: 100%;
            transition: transform 0.3s ease;
            &:hover {
                transform: scale(1.1);
        }
    }
    }
    &__text{
        padding: 20px;
        h2{
            margin-bottom: 20px;
        }
    }
}