@import '../../../variables.scss';
.tarif{
    padding: 140px 0 80px 0;
    text-align: center;

    &__title{
        position: relative;
        text-transform: uppercase;
        p{
            margin-bottom: 20px;
            &::before{
                left: 35%;
                position: absolute;
                width: 50px;
                height: 2px;
                background-color: $mainyellow;
                content: '';

            }
            &::after{
                right: 35%;
                position: absolute;
                width: 50px;
                height: 2px;
                background-color: $mainyellow;
                content: '';
            }
            
        }
        h2{
            margin-bottom: 50px;
        }

    }

    #tarif0{
        background:url('../../../media/img/tarif1.jpg') center (center/cover) no-repeat;
    }
    #tarif1{
        background:url('../../../media/img/tarif2.jpg') center (center/cover) no-repeat;
    }
    #tarif2{
        background:url('../../../media/img/tarif3.jpg') center (center/cover) no-repeat;
    }

    &__content{
        display: flex;
        justify-content: space-between;

    }
    &__wrapper{
        position: relative;
        width: 300px;
        height: 300px;
    }
    &__descr{
        position: absolute;
        width: 100%;
        height: 40%;
        bottom:0;
        background: #2f2f2fa6;
        text-align: center;
        text-transform: uppercase;
        font-weight: bold;
        color: $mainyellow;
        padding-top: 10px;

        p{
            margin-top: 20px;
        }
    }
}